@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    margin:0;
    padding:0;
    background:rgba(34, 71, 128);
}
nav{
    width: 100%;
    background:rgb(253, 252, 252);
    padding:10px 10px 4px 4px;
    position: fixed;
}
nav ul{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    -webkit-align-items:center;
            align-items:center;
    list-style: none;
    margin:0;
    padding:0;
}
nav li a{
    text-decoration: none;
    font-size: 22px;
    color: black;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
}
.news_div{
    display:grid;
    grid-template-rows: 0.8fr 1fr 0.15fr;
    min-height:100vh;
}
.news_content{
    padding:1rem;
    background:white;
}
.news_content h2{
    font-size:22px;
}
.news_content p{
    font-size:16px;
}
#news_time{
    color:grey;
}
.news_footer{
    color:white;
    background:linear-gradient(to right, rgba(34, 71, 128),rgb(90, 127, 182));
    padding:4px 10px;
}
.news_footer p{
    margin:0;
}
#more{
    font-size:20px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-weight: 200;
}
a{
    text-decoration: none;
}
#footer_tap{
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-weight: 200;
}
.news_img img{
    width:100%;
    height:100%;
    object-fit:cover;
}
.news_img{
    width:100%;
    height:100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.moreinfo_sec{
    height:100vh;
    display: none;
}
.moreinfo{
    margin-top:20px;
    padding:10px;
    color:white;
}
.back{
    text-align:center;
}
.moreinfo h1{
    font-family:'Courier New', Courier, monospace;
    font-size: 24px;
    font-weight:900;
    text-shadow: 2px 2px black;
}
#author{
    color: grey;
    padding: 0 10px;
    text-align:right;
}
.search{
    padding-top:15%;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items:center;
            align-items:center;
}
.search input{
    padding:8px;
    margin:10px 10px;
    font-size: 18px;
    border-radius:20px;
    outline: none;
    border:none;
    width:100%;
}
.topic{
    padding:5% 5%;
    background:white;
    overflow:auto;
    text-align:center;
    white-space:nowrap;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    -webkit-align-items:center;
            align-items:center;
    grid-gap:10%;
    gap:10%;
}
.topic_icon{
    display:inline-block;
}
.topic_icon p{
    margin:0;
}
.topic h1{
    color: white;
}
.topic i{
    color:rgb(65, 121, 226);
}
#topic_head{
    text-align:center;
    color:white;
}
#country_topic{
    text-align:center;
    padding:10px;
    font-weight:bold;
}
.setting_section{
    background:white;
    margin: 10px 0;
    padding:10px 10px;
}
.menu_row{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    padding:2% 8%; 
    -webkit-align-items:center; 
            align-items:center;
}
.menu_row label{
    font-size:20px;
}
hr{
    margin:0;
}
select{
    border:none;
    outline:none;
    background:none;
    color:rgb(32, 32, 148);
    font-size:18px;
    font-weight: bold;
}

