.search{
    padding-top:15%;
    display:flex;
    justify-content: center;
    align-items:center;
}
.search input{
    padding:8px;
    margin:10px 10px;
    font-size: 18px;
    border-radius:20px;
    outline: none;
    border:none;
    width:100%;
}
.topic{
    padding:5% 5%;
    background:white;
    overflow:auto;
    text-align:center;
    white-space:nowrap;
    display:flex;
    justify-content:space-between;
    align-items:center;
    gap:10%;
}
.topic_icon{
    display:inline-block;
}
.topic_icon p{
    margin:0;
}
.topic h1{
    color: white;
}
.topic i{
    color:rgb(65, 121, 226);
}
#topic_head{
    text-align:center;
    color:white;
}
#country_topic{
    text-align:center;
    padding:10px;
    font-weight:bold;
}
.setting_section{
    background:white;
    margin: 10px 0;
    padding:10px 10px;
}
.menu_row{
    display:flex;
    justify-content:space-between;
    padding:2% 8%; 
    align-items:center;
}
.menu_row label{
    font-size:20px;
}
hr{
    margin:0;
}
select{
    border:none;
    outline:none;
    background:none;
    color:rgb(32, 32, 148);
    font-size:18px;
    font-weight: bold;
}
