@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.news_div{
    display:grid;
    grid-template-rows: 0.8fr 1fr 0.15fr;
    min-height:100vh;
}
.news_content{
    padding:1rem;
    background:white;
}
.news_content h2{
    font-size:22px;
}
.news_content p{
    font-size:16px;
}
#news_time{
    color:grey;
}
.news_footer{
    color:white;
    background:linear-gradient(to right, rgba(34, 71, 128),rgb(90, 127, 182));
    padding:4px 10px;
}
.news_footer p{
    margin:0;
}
#more{
    font-size:20px;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-weight: 200;
}
a{
    text-decoration: none;
}
#footer_tap{
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-weight: 200;
}
.news_img img{
    width:100%;
    height:100%;
    object-fit:cover;
}
.news_img{
    width:100%;
    height:100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}